import { http } from '../utils/http';

export const getAllModules = () => {
    return new Promise((resolve, reject) => {
        http(process.env.REACT_APP_ROOT_URL_BATCHES, 'GET', `batches-service/batches/profiles?status=ACTIVE`, true)
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};

